<template>
  <div id="app">
    <!--//头部内容-->
  <mt-header title="简单管理系统"></mt-header>
   <router-view></router-view>




    <!--//底部内容-->
    <mt-tabbar  v-model="selected">
      <mt-tab-item id="tab1">
        首页
        <img slot="icon" src="./assets/img/icon/sy.png">
      </mt-tab-item>
      <mt-tab-item id="tab2">
        会员
        <img slot="icon" src="./assets/img/icon/icon_signal.png"/>
      </mt-tab-item>
      <mt-tab-item id="tab3">

        购物车
        <img slot="icon" src="./assets/img/icon/gwc.png">
      </mt-tab-item>
      <mt-tab-item id="tab4">

        查找
        <img slot="icon" src="./assets/img/icon/icon_search.png">
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
  import MtButton from "../node_modules/mint-ui/packages/button/src/button";
  import MtHeader from "../node_modules/mint-ui/packages/header/src/header";
export default {
  components: {
    MtHeader,
    MtButton},
  data(){
        return{

        txte:'hello',
          selected:''
        }
    },
  created(){

  },
  watch:{
      selected:function (newid,oldid) {
          console.log(newid)
        if(newid==='tab4'){
            this.$router.push({name:'select'})
        }else if(newid==='tab1'){
            this.$router.push({name:'home'})
        }
        else if(newid==='tab2'){
          this.$router.push({name:'vip'})
        }
        else if(newid==='tab3'){
          this.$router.push({name:'gouwu'})
        }
      }
  }

}
</script>

<style>
*{
  padding: 0;
  margin: 0;
}

</style>



