<template>
  <div>
    <!--新闻资讯头部-->
    <mt-header fixed title="新闻资讯">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回首页</mt-button>
      </router-link>
    </mt-header>
    <ul class="newlist">
      <li>
        <div v-for="(data,index) in newslist" :key="index">
          <div>
            <img :src="data.img">
            <ul>
              <li v-text="data.titles"></li>
              <li v-text="data.file"></li>
            </ul>
          </div>

        </div>
      </li>

    </ul>
    这是新闻资讯页面
  </div>
</template>
<script>
    export default{
        data(){
            return{
          newslist:[]
          }
        },
    methods:{
      backs:function () {

      }
    },
      created(){
        this.$Axios.get('newlist')
          .then((res)=>{
            console.log(res)
            this.newslist=res.data
          })
      }
    }
</script>
<style>
.newlist li div{
  height: 65px;
}
.newlist li div img{
  height: 100%;
}
.newlist li div ul{
  float: right;
  /*background-color: blueviolet;*/
  width: 82%;
  list-style: none;
  height: 96%;
  border-bottom: 1px solid #cccccc;
}
.newlist li div ul li{

  height: 50%;
  line-height: 32.5px;
}
</style>



